
import LoadingAnimateVue from './LoadingAnimate.vue'
import { defineComponent } from 'vue'

export default defineComponent({
    components: {
        LoadingAnimateVue
    },
    props: {
        targetFile: {
            type: String,
            required: true
        }
    },
    data() {
        return {
            isArtLoading: true,
        }
    },
    async mounted() {
        this.isArtLoading = await this.falseOperation()
    },
    methods: {
        falseOperation: async function(): Promise<boolean> {
            return new Promise(resolve => {
                window.addEventListener('message', function(event) {
                    console.log(event.data)
                    if (event.data === "statueLoaded") {
                        console.log("statueLoaded")
                        resolve(false)
                    } else if (event.data === "distortionLoaded") {
                        console.log("distortionLoaded")
                        resolve(false)
                    }
                 })
            })
        }
    }
})
