
import { defineComponent } from 'vue';
import ModalWindowVue from './ModalWindow.vue';
import AboutContent from '../modalwindows/AboutContent.vue'
import SkillContent from "../modalwindows/SkillContent.vue"
import LogsContent from "../modalwindows/LogsContent.vue"
import TwitterEmbed from '@/modalwindows/TwitterEmbed.vue';
import LoadingArtProcess from './LoadingArtProcess.vue';

interface ModalWindowTypes {
  About: boolean,
  Skill: boolean,
  Logs: boolean,
  Twitter: boolean,
  Distortion: boolean,
  Questions: boolean,
  isTwitterLoading: boolean,
  Synthwave: boolean
}

interface CurrentOpenIndexType {
  currentOpenIndex: number
}

export default defineComponent({
  components: {
    ModalWindowVue,
    AboutContent,
    SkillContent,
    LogsContent,  
    TwitterEmbed,
    LoadingArtProcess
  },
  data(): ModalWindowTypes & CurrentOpenIndexType{
    return {
      currentOpenIndex: 0,
      About: false,
      Skill: false,
      Logs: false,
      Twitter: false,
      Distortion: false,
      Questions: false,
      Synthwave:false,
      isTwitterLoading: true
    }
  },
  mounted() {
    const iconElements = document.getElementsByClassName("isModal")
    const iconElementsTrigger = Array.from(iconElements)
    for (let i = 0; i < iconElementsTrigger.length; i++) {
      const element = iconElementsTrigger[i] as HTMLElement;
      if (this.isSmartPhone()) {
        element.addEventListener("click", this.ModalWindowDisplay)    
      } else {
        element.addEventListener("dblclick", this.ModalWindowDisplay)    
      }
    }
  },
  methods: {
    ModalWindowDisplay: function(e: any) {
      console.log("execute")
      const ModalWindows = document.getElementsByClassName("modal-window")
      for (let i = 0; i < ModalWindows.length; i++) {
        const element = ModalWindows[i] as HTMLElement;
        element.style.zIndex = "0";
      }
      const dataType: keyof ModalWindowTypes = e.target?.dataset?.type
      // const targetWindow = document.getElementById(dataType) as HTMLElement;
      this[dataType] = true

      this.waitForOpenWindow(dataType,(targetWindow) => {
        console.log(targetWindow)
        console.log(e.target.dataset.type)
      
        targetWindow.style.zIndex = "1";


        const screenElement = document.getElementById("screen") as HTMLElement;
        const screenWidth = parseFloat(getComputedStyle(screenElement).width)
        const screenHeight = parseFloat(getComputedStyle(screenElement).height)

        const range =  (this.currentOpenIndex * 50)
        // targetWindow.style.left = Number(targetWindow.style.left.replace("px", "")) + range + "px"
        targetWindow.style.left = (screenWidth / 2) + range + "px"
        targetWindow.style.top = (screenHeight / 2) + range + "px"
        this.currentOpenIndex += 1;
      })
    },
    ModalWindowHidden: function(windowType: keyof ModalWindowTypes) {
      this[windowType] = false
      this.currentOpenIndex -= 1;
    },
    isSmartPhone: function() {
      if (navigator.userAgent.match(/iPhone|Android.+Mobile/)) {
        return true;
      } else {
        return false;
      }
    },
    TwitterLoadingController: function(value: boolean) {
      this.isTwitterLoading = value
    },
    waitForOpenWindow: function(selector: string, callback: (element: HTMLElement) => void): void {
      const container = document.getElementById("drawWindow");
      
      if (!container) {
        throw new Error("Container element not found");
      }

      const observer = new MutationObserver((mutationsList) => {
        for (const mutation of mutationsList) {
          if (mutation.type === "childList") {
            const targetElement = document.getElementById(selector) as HTMLElement;
            if (targetElement) {
              observer.disconnect();
              callback(targetElement as HTMLElement);
              break;
            }
          }
        }
      });
    observer.observe(container, { childList: true });
  }
  }
})
